.aboutMePn{
	margin-top: 213px;
	margin-bottom: 80px;
	.aboutMeInner{
		width: 1000px;
		margin:0 auto;
		.col1{
			width: 200px;
			float: left;
			.aboutMe{
				background: #f5f5f5;
				border: 2px solid #f3f3f3;
				border-radius: 5px;
				padding: 0 18px;
				.bd{
					li{
						position: relative;
						padding: 0 18px;
						line-height: 30px;
						margin-bottom: 10px;
						cursor: pointer;
						a{
							color: #999;
						}
					}
					.firstTit{
						margin-top: 20px;
						margin-bottom: 10px;
						color: #555;
						font-size: 16px;
					}
					.select{
						a{color: #3aaae3;}
						background: #fff;
						
					}
				}
			}
		}
		.col2{
			float: right;
			width: 750px;
			.hd{
				font-size: 24px;
				padding-bottom: 30px;
				border-bottom: 1px solid #f3f3f3;
				
			}
			.bd{
				padding-top: 30px;
				.line{
					line-height: 24px;
					margin-bottom: 30px;
					.tit{
						color: #555;
					}
					a{
						color: #3aaae3
					}
				}
				.mb10{
					margin-bottom: 10px;
				}
				.autoWidth{
					width: 100%;
					max-width: 100%;

				}
				.listType{
					padding-left: 20px;
					line-height: 30px;
					li{
						list-style-type:circle;
					}	
				}
				.qzImg{
					display: block;
					margin: 20px 0;
				}
			}
		}
	}
	
}
.attentionTab{
	height: 40px;
	border-bottom: 1px solid #e8e8e8;
	*overflow: hidden;
	li{
		float: left;
		padding:0 20px;
		cursor: pointer;
		color: #999;
		height: 39px;
		&:hover{
			color:#555;
			border-bottom: 3px solid #3aa9e1;
		}
	}
	.select{
		color: #555;
		border-bottom: 3px solid #3aa9e1;
		position: relative;
		
	}
}
.exampleSingle{
	li{
		margin-bottom: 20px;
		background: #f5f5f5;
		border-radius: 5px;
		color: #999;
		padding:10px 20px;
		line-height: 26px;
		.tit{
			color: #555;
		}
	}
}